export default {
	data() {
		return {
			status: [
				{
					key: 1,
					name: '我的订单',
					icon: '/static/fumou-center-template/one.png',
					url: ''
				},
				{
					key: 2,
					name: '报事工单',
					icon: '/static/fumou-center-template/2.png',
					url: ''
				},
				{
					key: 3,
					name: '发票下载',
					icon: '/static/fumou-center-template/3.png',
					url: 'user/invoice/invoice'
				},
				{
					key: 4,
					name: '我的余额',
					icon: '/static/fumou-center-template/4.png',
					url: 'user/balance/balance'
				}
			],
			menus: [
				{
					name: '我的房产',
					icon: '/static/fumou-center-template/5.png',
					key: 1,
					url: 'user/my-house/my-house'
				},
				{
					name: '我的车辆',
					icon: '/static/fumou-center-template/6.png',
					key: 2,
					url: ''
				},
				{
					name: '活动预约',
					icon: '/static/fumou-center-template/7.png',
					key: 3,
					url: ''
				},
				{
					name: '报事报修',
					icon: '/static/fumou-center-template/8.png',
					key: 4,
					url: ''
				},
				{
					name: '投诉建议',
					icon: '/static/fumou-center-template/9.png',
					key: 5,
					url: ''
				},
				{
					name: '联系物业',
					icon: '/static/fumou-center-template/10.png',
					key: 6,
					url: 'main/call/call'
				}
			]
		};
	},
	methods: {
		getNav(url) {
			if (!this.$store.state.hasBoundHouse) {
				uni.showModal({
					content: '请先绑定房产',
					success(res) {
						if (res.confirm) {
							uni.navigateTo({
								url: '/pages/reg/bound-house/bound-house'
							});
						}
					}
				});
				return;
			}
			if (!url) {
				uni.showToast({
					icon: 'none',
					title: '正在开发...'
				});
				return;
			}
			uni.navigateTo({
				url: '/pages/' + url
			});
		}
	},
	computed: {}
};
